import React, { useState, useEffect, useRef } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Avatar, AvatarFallback } from '@/components/ui/avatar';
import { Badge } from '@/components/ui/badge';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
} from '@/components/ui/dialog';
import {
  Send,
  Phone,
  Mail,
  X,
  MessageSquare,
  Check,
  CheckCheck,
  AlertCircle,
  Loader2,
  Minimize2,
  Maximize2,
  Plus
} from 'lucide-react';
import { cn } from '@/lib/utils';
import { toast } from 'sonner';
import {
  sendSMSToContact,
  getContactMessages,
  checkNewIncomingMessages,
  formatPhoneToE164,
  formatPhoneForDisplay
} from '@/api/services/messaging/messagingService';
import { getAllPhoneNumbers } from '@/api/services/phoneNumbers/phoneNumberService';
import CreatePhoneNumberForm from '@/components/PhoneNumbers/CreatePhoneNumberForm';

interface Message {
  id: string;
  content: string;
  sender: 'user' | 'contact';
  timestamp: Date;
  status: 'sending' | 'sent' | 'delivered' | 'failed' | 'read';
  messageId?: string;
}

interface Contact {
  id: string;
  email: string;
  contact_number: string;
  list_name?: string;
}

interface ChatSidebarProps {
  isOpen: boolean;
  contact: Contact | null;
  onClose: () => void;
}

const ChatSidebar: React.FC<ChatSidebarProps> = ({ isOpen, contact, onClose }) => {
  const [messages, setMessages] = useState<Message[]>([]);
  const [newMessage, setNewMessage] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [isSending, setIsSending] = useState(false);
  const [isMinimized, setIsMinimized] = useState(false);
  const [phoneNumbers, setPhoneNumbers] = useState<any[]>([]);
  const [selectedFromNumber, setSelectedFromNumber] = useState<string>('');
  const [isCreatePhoneModalOpen, setIsCreatePhoneModalOpen] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // Auto-scroll to bottom when new messages arrive
  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  // Load message history when contact changes
  useEffect(() => {
    if (contact && isOpen) {
      loadMessageHistory();
    }
  }, [contact, isOpen]);

  // Set up efficient polling for incoming messages only
  useEffect(() => {
    if (!contact || !isOpen || !contact.id) return;

    // Set up polling every 2 seconds for new incoming messages only
    const pollInterval = setInterval(async () => {
      try {
        // Get the last message ID to check for new messages after it
        const lastMessage = messages[messages.length - 1];
        const lastMessageId = lastMessage?.id;

        console.log(`🔍 Polling for new incoming messages for contact ${contact.id} after ID: ${lastMessageId}`);

        const response = await checkNewIncomingMessages(contact.id, lastMessageId);

        if (response.success && response.hasNewMessages && response.messages.length > 0) {
          console.log(`📨 Found ${response.messages.length} new incoming messages`);

          // Add new incoming messages to the existing messages
          const newMessages = response.messages.map(msg => ({
            id: msg.id.toString(),
            content: msg.content,
            sender: msg.sender as 'user' | 'contact',
            timestamp: new Date(msg.timestamp),
            status: msg.status as 'sending' | 'sent' | 'delivered' | 'failed' | 'read',
            messageId: msg.messageId
          }));

          setMessages(prev => [...prev, ...newMessages]);

          // Show notification for new messages
          toast.success(`New message from ${contact.email?.split('@')[0] || 'Contact'}`);
        }
      } catch (error) {
        console.error('Error polling for new messages:', error);
        // Don't show error toast to avoid spam
      }
    }, 2000);

    return () => {
      clearInterval(pollInterval);
    };
  }, [contact?.id, isOpen, messages]);

  // Load user's phone numbers
  const loadPhoneNumbers = async () => {
    try {
      const response = await getAllPhoneNumbers();
      if (response.success && response.data) {
        setPhoneNumbers(response.data);
        // Auto-select first phone number if available
        if (response.data.length > 0 && !selectedFromNumber) {
          setSelectedFromNumber(response.data[0].number);
        }
      }
    } catch (error) {
      console.error('Failed to load phone numbers:', error);
    }
  };

  // Load phone numbers when component mounts
  useEffect(() => {
    if (isOpen) {
      loadPhoneNumbers();
    }
  }, [isOpen]);

  // Reset state when sidebar closes
  useEffect(() => {
    if (!isOpen) {
      setMessages([]);
      setNewMessage('');
      setIsMinimized(false);
    }
  }, [isOpen]);

  const loadMessageHistory = async () => {
    if (!contact) return;
    
    setIsLoading(true);
    try {
      const response = await getContactMessages(contact.id);
      if (response.success) {
        const formattedMessages: Message[] = response.messages.map((msg: any) => ({
          id: msg.id.toString(),
          content: msg.content,
          sender: msg.sender,
          timestamp: new Date(msg.timestamp),
          status: msg.status,
          messageId: msg.message_id
        }));
        setMessages(formattedMessages);
      } else {
        setMessages([]);
      }
    } catch (error) {
      console.error('Failed to load message history:', error);
      toast.error('Failed to load message history');
      setMessages([]);
    } finally {
      setIsLoading(false);
    }
  };

  const sendMessage = async () => {
    if (!newMessage.trim() || !contact || isSending) return;

    const messageContent = newMessage.trim();
    setNewMessage('');
    setIsSending(true);

    // Create temporary message with sending status
    const tempMessage: Message = {
      id: Date.now().toString(),
      content: messageContent,
      sender: 'user',
      timestamp: new Date(),
      status: 'sending'
    };

    setMessages(prev => [...prev, tempMessage]);

    try {
      // Format phone number to E.164 format
      const formattedPhone = formatPhoneToE164(contact.contact_number);
      
      // Send SMS using the messaging service with selected phone number
      const result = await sendSMSToContact({
        toPhoneNumber: formattedPhone,
        message: messageContent,
        contactId: contact.id,
        fromPhoneNumber: selectedFromNumber || undefined
      });
      
      if (result.success) {
        // Update message status to sent
        setMessages(prev => 
          prev.map(msg => 
            msg.id === tempMessage.id 
              ? { ...msg, status: 'sent', messageId: result.messageId }
              : msg
          )
        );
        toast.success('Message sent successfully');
      } else {
        throw new Error(result.error || 'Failed to send message');
      }
    } catch (error) {
      console.error('Failed to send message:', error);
      
      // Update message status to failed
      setMessages(prev => 
        prev.map(msg => 
          msg.id === tempMessage.id 
            ? { ...msg, status: 'failed' }
            : msg
        )
      );
      
      toast.error('Failed to send message');
    } finally {
      setIsSending(false);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  const getInitials = (email: string) => {
    const name = email.split('@')[0];
    return name.substring(0, 2).toUpperCase();
  };

  const formatTime = (date: Date) => {
    const now = new Date();
    const messageDate = new Date(date);

    // Check if the message is from today
    const isToday = now.toDateString() === messageDate.toDateString();

    // Check if the message is from yesterday
    const yesterday = new Date(now);
    yesterday.setDate(yesterday.getDate() - 1);
    const isYesterday = yesterday.toDateString() === messageDate.toDateString();

    if (isToday) {
      return messageDate.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
    } else if (isYesterday) {
      return `Yesterday ${messageDate.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}`;
    } else {
      // For older messages, show date and time
      return messageDate.toLocaleDateString([], {
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    }
  };

  const getMessageStatusIcon = (status: Message['status']) => {
    switch (status) {
      case 'sending':
        return <Loader2 className="h-3 w-3 animate-spin text-gray-400" />;
      case 'sent':
        return <Check className="h-3 w-3 text-gray-400" />;
      case 'delivered':
        return <CheckCheck className="h-3 w-3 text-blue-500" />;
      case 'failed':
        return <AlertCircle className="h-3 w-3 text-red-500" />;
      default:
        return null;
    }
  };

  if (!contact) return null;

  return (
    <>
      {/* Overlay */}
      {isOpen && (
        <div 
          className="fixed inset-0 bg-black/20 z-40 lg:hidden"
          onClick={onClose}
        />
      )}

      {/* Sidebar */}
      <div className={cn(
        "fixed top-0 right-0 h-full w-full sm:w-3/5 lg:w-1/2 xl:w-2/5 bg-white shadow-2xl z-50 transform transition-transform duration-300 ease-in-out flex flex-col border-l border-gray-200",
        isOpen ? "translate-x-0" : "translate-x-full"
      )}>
        {/* Header */}
        <div className="p-6 border-b bg-gradient-to-r from-slate-50 via-blue-50 to-indigo-50 flex-shrink-0 shadow-sm">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <div className="relative">
                <Avatar className="h-16 w-16 bg-gradient-to-br from-blue-500 via-indigo-500 to-purple-600 shadow-lg ring-2 ring-white">
                  <AvatarFallback className="text-white font-bold text-xl">
                    {getInitials(contact.email)}
                  </AvatarFallback>
                </Avatar>
                <div className="absolute -bottom-1 -right-1 h-5 w-5 bg-green-500 border-2 border-white rounded-full shadow-sm"></div>
              </div>
              <div className="flex-1 min-w-0">
                <h3 className="font-bold text-gray-900 text-xl mb-1 truncate">
                  {contact.email.split('@')[0]}
                </h3>
                <div className="space-y-1.5">
                  <div className="flex items-center space-x-2 text-sm">
                    <div className="flex items-center space-x-1.5 bg-white/70 backdrop-blur-sm rounded-full px-2.5 py-1 shadow-sm">
                      <Mail className="h-3.5 w-3.5 text-blue-500" />
                      <span className="truncate font-medium text-gray-700 max-w-[200px]">{contact.email}</span>
                    </div>
                  </div>
                  <div className="flex items-center space-x-2 text-sm">
                    <div className="flex items-center space-x-1.5 bg-white/70 backdrop-blur-sm rounded-full px-2.5 py-1 shadow-sm">
                      <Phone className="h-3.5 w-3.5 text-green-500" />
                      <span className="font-medium text-gray-700">{formatPhoneForDisplay(contact.contact_number)}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="flex items-center space-x-2">
              {contact.list_name && (
                <Badge variant="secondary" className="text-xs bg-gradient-to-r from-blue-100 to-indigo-100 text-blue-700 border-blue-200 shadow-sm">
                  {contact.list_name}
                </Badge>
              )}
              <Button
                variant="ghost"
                size="sm"
                onClick={() => setIsMinimized(!isMinimized)}
                className="h-9 w-9 p-0 hover:bg-white/80 hover:shadow-sm rounded-full transition-all duration-200"
              >
                {isMinimized ? <Maximize2 className="h-4 w-4 text-gray-600" /> : <Minimize2 className="h-4 w-4 text-gray-600" />}
              </Button>
              <Button
                variant="ghost"
                size="sm"
                onClick={onClose}
                className="h-9 w-9 p-0 hover:bg-red-50 hover:shadow-sm text-gray-600 hover:text-red-600 rounded-full transition-all duration-200"
              >
                <X className="h-4 w-4" />
              </Button>
            </div>
          </div>
        </div>

        {!isMinimized && (
          <>
            {/* Messages Area */}
            <div className="flex-1 flex flex-col min-h-0">
              <div className="flex-1 overflow-y-auto p-6 bg-gradient-to-b from-gray-50/50 to-white" style={{ maxHeight: 'calc(100vh - 280px)' }}>
                {isLoading ? (
                  <div className="flex flex-col items-center justify-center h-full text-gray-400">
                    <Loader2 className="h-8 w-8 animate-spin mb-4 text-blue-500" />
                    <p className="text-center text-sm">Loading messages...</p>
                  </div>
                ) : messages.length === 0 ? (
                  <div className="flex flex-col items-center justify-center h-full text-gray-400">
                    <div className="w-20 h-20 bg-gradient-to-br from-blue-100 to-indigo-100 rounded-full flex items-center justify-center mb-4 shadow-sm">
                      <MessageSquare className="h-10 w-10 text-blue-500" />
                    </div>
                    <p className="text-center text-sm font-medium text-gray-600">
                      No messages yet
                    </p>
                    <p className="text-center text-xs text-gray-400 mt-1">
                      Start a conversation with {contact.email.split('@')[0]}!
                    </p>
                  </div>
                ) : (
                  <div className="space-y-4">
                    {messages.map((message) => (
                      <div
                        key={message.id}
                        className={cn(
                          'flex',
                          message.sender === 'user' ? 'justify-end' : 'justify-start'
                        )}
                      >
                        <div
                          className={cn(
                            'max-w-[85%] rounded-2xl px-4 py-3 shadow-sm relative',
                            message.sender === 'user'
                              ? 'bg-gradient-to-r from-blue-500 to-indigo-600 text-white'
                              : 'bg-white text-gray-900 border border-gray-200 shadow-md'
                          )}
                        >
                          <p className="text-sm leading-relaxed">{message.content}</p>
                          <div className={cn(
                            'flex items-center justify-end space-x-1.5 mt-2',
                            message.sender === 'user' ? 'text-white/80' : 'text-gray-500'
                          )}>
                            <span className="text-xs font-medium">{formatTime(message.timestamp)}</span>
                            {message.sender === 'user' && getMessageStatusIcon(message.status)}
                          </div>
                          {/* Message tail */}
                          <div className={cn(
                            'absolute top-3 w-3 h-3 transform rotate-45',
                            message.sender === 'user'
                              ? 'bg-gradient-to-r from-blue-500 to-indigo-600 -right-1.5'
                              : 'bg-white border-r border-b border-gray-200 -left-1.5'
                          )} />
                        </div>
                      </div>
                    ))}
                    <div ref={messagesEndRef} />
                  </div>
                )}
              </div>
            </div>

            {/* Message Input */}
            <div className="p-6 border-t bg-gradient-to-r from-gray-50 to-white flex-shrink-0 shadow-lg">
              {/* Phone Number Selector or Add Phone Number Link */}
              {phoneNumbers.length > 0 ? (
                <div className="mb-4">
                  <label className="block text-sm font-semibold text-gray-700 mb-2 flex items-center space-x-2">
                    <Phone className="h-4 w-4 text-blue-500" />
                    <span>Send from phone number:</span>
                  </label>
                  <Select value={selectedFromNumber} onValueChange={setSelectedFromNumber}>
                    <SelectTrigger className="w-full border-gray-300 focus:border-blue-500 focus:ring-blue-500 rounded-lg shadow-sm">
                      <SelectValue placeholder="Select phone number" />
                    </SelectTrigger>
                    <SelectContent>
                      {phoneNumbers.map((phone) => (
                        <SelectItem key={phone.id} value={phone.number}>
                          <div className="flex items-center space-x-2">
                            <span className="font-medium">{formatPhoneForDisplay(phone.number)}</span>
                            {phone.friendlyName && (
                              <span className="text-gray-500 text-sm">({phone.friendlyName})</span>
                            )}
                          </div>
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
              ) : (
                <div className="mb-4">
                  <label className="block text-sm font-semibold text-gray-700 mb-2 flex items-center space-x-2">
                    <Phone className="h-4 w-4 text-blue-500" />
                    <span>Phone number required to send SMS:</span>
                  </label>
                  <div className="flex items-center justify-center p-4 border-2 border-dashed border-gray-300 rounded-lg bg-gray-50 hover:bg-gray-100 transition-colors">
                    <Button
                      onClick={() => setIsCreatePhoneModalOpen(true)}
                      variant="ghost"
                      className="flex items-center space-x-2 text-blue-600 hover:text-blue-700 font-medium"
                    >
                      <Plus className="h-4 w-4" />
                      <span>Add Phone Number</span>
                    </Button>
                  </div>
                </div>
              )}

              <div className="flex items-end space-x-3">
                <div className="flex-1">
                  <Input
                    value={newMessage}
                    onChange={(e) => setNewMessage(e.target.value)}
                    onKeyPress={handleKeyPress}
                    placeholder={phoneNumbers.length === 0 ? "Add a phone number to send messages..." : "Type your message..."}
                    className="w-full border-gray-300 focus:border-blue-500 focus:ring-blue-500 rounded-xl px-4 py-3 shadow-sm resize-none"
                    disabled={isSending || phoneNumbers.length === 0}
                  />
                </div>
                <Button
                  onClick={sendMessage}
                  disabled={!newMessage.trim() || isSending || !selectedFromNumber || phoneNumbers.length === 0}
                  size="sm"
                  className="px-4 py-3 bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-700 hover:to-indigo-700 text-white rounded-xl shadow-md transition-all duration-200 hover:shadow-lg"
                >
                  {isSending ? (
                    <Loader2 className="h-5 w-5 animate-spin" />
                  ) : (
                    <Send className="h-5 w-5" />
                  )}
                </Button>
              </div>
              <div className="mt-3 flex items-center justify-between">
                <p className="text-xs text-gray-500 flex items-center space-x-1">
                  <MessageSquare className="h-3 w-3" />
                  <span>
                    {selectedFromNumber
                      ? `From ${formatPhoneForDisplay(selectedFromNumber)} to ${formatPhoneForDisplay(contact.contact_number)}`
                      : `SMS to ${formatPhoneForDisplay(contact.contact_number)}`
                    }
                  </span>
                </p>
                {messages.length > 0 && (
                  <p className="text-xs text-gray-400">
                    {messages.length} message{messages.length !== 1 ? 's' : ''}
                  </p>
                )}
              </div>
            </div>
          </>
        )}
      </div>

      {/* Create Phone Number Modal */}
      <Dialog open={isCreatePhoneModalOpen} onOpenChange={setIsCreatePhoneModalOpen}>
        <DialogContent className="max-w-7xl max-h-[90vh] overflow-y-auto">
          <DialogHeader>
            <DialogTitle>Create New Phone Number</DialogTitle>
            <DialogDescription>
              Get a free AI CRUITMENT phone number to send SMS messages.
            </DialogDescription>
          </DialogHeader>
          <CreatePhoneNumberForm
            onSuccess={() => {
              setIsCreatePhoneModalOpen(false);
              // Reload phone numbers after successful creation
              loadPhoneNumbers();
            }}
            onCancel={() => setIsCreatePhoneModalOpen(false)}
          />
        </DialogContent>
      </Dialog>
    </>
  );
};

export default ChatSidebar;
